@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
footer {
  background-color: var(--black-color);
  height: 120px;
}
footer .container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

footer h1 {
  font-size: 2em;
  color: var(--white-color);
  font-weight: bold;
}

.section {
  margin-bottom: 80px;
  margin-top: 20px;
}
.section > * ~ * {
  margin-top: 50px;
}

.section .section-title {
  font-size: 2rem;
  text-transform: capitalize;
  text-align: center;
  color: var(--second-color);
}

.input-container {
  display: flex;
  flex-direction: column;
}
.input-container label {
  margin: 0 0 10px 0;
  text-transform: capitalize;
}
.input-container ~ .input-container {
  margin-top: 2rem;
}
input {
  padding: 10px;
  outline: none;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid var(--black-color);
}

.form {
  padding: 0 50px;
  max-width: 600px;
  margin: auto;
}
.select-container label,
.radio-container label,
.checkbox-container > label,
.textarea-container label {
  display: block;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.select-container select {
  width: 50px;
}
.select-container select:nth-of-type(3) {
  width: 80px;
}
.select-container select ~ select {
  margin-left: 1rem;
}
.radio-container {
  display: flex;
  flex-wrap: wrap;
}
.radio-container label {
  width: 100%;
}
.radio-item {
  display: flex;
}
.radio-item ~ .radio-item {
  margin-left: 1rem;
}
.radio-item > p {
  margin-right: 5px;
}
.city {
  width: 100px !important;
}
.checkbox-container {
  display: flex;
  flex-wrap: wrap;
}
.checkbox-container > label {
  width: 100%;
}
.checkbox-item {
  display: flex;
  align-items: center;
}
.checkbox-item > label {
  margin-right: 5px;
}
.checkbox-item ~ .checkbox-item {
  margin-left: 1rem;
}
.textarea-container textarea {
  width: 100%;
  padding: 1rem;
}
button {
  display: inline-block;
  text-align: center;
  border: 0;
  background-color: var(--second-color);
  color: var(--white-color);
  border-radius: 5px;
  padding: 1rem 2rem;
  width: auto;
  cursor: pointer;
  outline: none;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 1rem;
  margin-right: 1rem;
}

header {
  background-color: var(--main-color);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  height: 120px;
}
header .container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

header h1 {
  font-size: 2em;
  color: var(--white-color);
  font-weight: bold;
}

nav {
  background-color: rgba(204, 204, 204, 0.349);
}
nav ul {
  padding: 20px;
  color: var(--black-color);
}
nav ul li {
  width: 100%;
  border-radius: 1rem;
  cursor: pointer;
  padding-left: 10px;
}
nav ul li ~ li {
  margin-top: 1rem;
}
nav ul li.active {
  background-color: #fff;
}
nav ul li a {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  font-weight: 500;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --main-color: #0092ba;
  --second-color: #eb404e;
  --green-color: #d9ebe9;
  --black-color: #004a43;
  --white-color: #fff;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
}

a {
  text-decoration: none;
  color: unset;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}
.main-container {
  display: flex;
  align-items: stretch;
  margin-top: 120px;
  min-height: 100vh;
}
.main-container nav {
  width: 20%;
}
.main-container .main {
  flex-grow: 1;
}
.map,
.fanpage {
  padding: 50px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

